$myHeight: 80vh;
$offcanvas-width: 30%;
$yellow: #ffc107;
$article-detail-width: 100%;

.box {

    .logo {
        height: 3rem;
        width: auto;
    }

    .title-list {
        // max-height: $myHeight;
        // height: $myHeight;
        overflow-y: scroll;

        li {
            background: none;
            // transition: background-color .1s;

            // &:hover {
            //     background-color: #0066CC;
            //     cursor: pointer;
            // }
        }

    }

    .article {
        &:hover {
            color: black !important;
            background-color: $yellow !important;
            cursor: pointer;
        }
    }

    img.article-detail {
        width: $article-detail-width;
        max-width: $article-detail-width;
        overflow: scroll;
    }

    .offcanvas {
        width: $offcanvas-width !important;
    }

    object {
        height: $myHeight;
    }

    .selectedArticle {
        color: black !important;
        background-color: $yellow !important;
    }

    .goTopBtn {
        position: fixed !important;
        bottom: 1rem;
        right: 1rem;
    }

    .showMenuBtn {
        position: fixed !important;
        bottom: 4rem;
        right: 1rem;
    }

}